const initialState = {
    currentUser: [],
}

const reducer = (state = initialState, action:any) => {
    switch (action.type) {
        case 'LOGIN_USER':
            console.log(action.payload);
            return {
                currentUser: action.payload.Username,
            };
        case 'REGISTR_USER':
            console.log(action.payload);
            return {currentUser: action.payload.Username}
        default: 
            return state;
    }
}

export default reducer;